/* Codigo Base */
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   ========================================================================== */
/**
 * Remove the margin in all browsers.
 */
body {
  margin: 0;
}

/**
 * Render the `main` element consistently in IE.
 */
main {
  display: block;
}

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Firefox, and Safari.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
   ========================================================================== */
/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge and IE.
 */
hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
   ========================================================================== */
/**
 * Remove the gray background on active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * 1. Remove the bottom border in Chrome 57-
 * 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
 */
abbr[title] {
  border-bottom: none; /* 1 */
  text-decoration: underline; /* 2 */
  text-decoration: underline dotted; /* 2 */
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */
b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 80%;
}

/**
 * Prevent `sub` and `sup` elements from affecting the line height in
 * all browsers.
 */
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/* Embedded content
   ========================================================================== */
/**
 * Remove the border on images inside links in IE 10.
 */
img {
  border-style: none;
}

/* Forms
   ========================================================================== */
/**
 * 1. Change the font styles in all browsers.
 * 2. Remove the margin in Firefox and Safari.
 */
button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

/**
 * Show the overflow in IE.
 * 1. Show the overflow in Edge.
 */
button,
input { /* 1 */
  overflow: visible;
}

/**
 * Remove the inheritance of text transform in Edge, Firefox, and IE.
 * 1. Remove the inheritance of text transform in Firefox.
 */
button,
select { /* 1 */
  text-transform: none;
}

/**
 * Correct the inability to style clickable types in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Remove the inner border and padding in Firefox.
 */
button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus styles unset by the previous rule.
 */
button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Correct the padding in Firefox.
 */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * 1. Correct the text wrapping in Edge and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 * 3. Remove the padding so developers are not caught out when they zero out
 *    `fieldset` elements in all browsers.
 */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  padding: 0; /* 3 */
  white-space: normal; /* 1 */
}

/**
 * Add the correct vertical alignment in Chrome, Firefox, and Opera.
 */
progress {
  vertical-align: baseline;
}

/**
 * Remove the default vertical scrollbar in IE 10+.
 */
textarea {
  overflow: auto;
}

/**
 * 1. Add the correct box sizing in IE 10.
 * 2. Remove the padding in IE 10.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Chrome.
 */
[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

/**
 * 1. Correct the odd appearance in Chrome and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Remove the inner padding in Chrome and Safari on macOS.
 */
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style clickable types in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/* Interactive
   ========================================================================== */
/*
 * Add the correct display in Edge, IE 10+, and Firefox.
 */
details {
  display: block;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Misc
   ========================================================================== */
/**
 * Add the correct display in IE 10+.
 */
template {
  display: none;
}

/**
 * Add the correct display in IE 10.
 */
[hidden] {
  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 1.6rem;
}

.contenedor {
  width: 90%;
  max-width: 115rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Open Sans", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  line-height: 1.4;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  h1 {
    font-size: 3.8rem;
  }
}

h2 {
  font-size: 3.4rem;
  line-height: 1.5;
}

h3 {
  font-size: 2.9rem;
}

h4 {
  font-size: 2.4rem;
}

body.dark-mode {
  background-color: #1a1a1a;
}
body.dark-mode h1, body.dark-mode h2, body.dark-mode h3, body.dark-mode h4 {
  color: #e1e1e1;
}
body.dark-mode p,
body.dark-mode a {
  color: #FFFFFF;
}
body.dark-mode .anuncio,
body.dark-mode .resumen-propiedad {
  background-color: #333333;
  border: none;
}
body.dark-mode .resumen-propiedad {
  padding: 0 1.5rem;
  border: none;
}
body.dark-mode .iconos-caracteristicas img {
  filter: invert(100%);
}
body.dark-mode .entrada-blog {
  background-color: #000000;
}
body.dark-mode .informacion-nosotros blockquote {
  color: #71B100;
}
body.dark-mode .formulario {
  background-color: #1a1a1a;
}
body.dark-mode .formulario fieldset {
  border: 1.5px solid #e1e1e1;
}
body.dark-mode .formulario legend,
body.dark-mode .formulario label,
body.dark-mode .formulario p {
  color: #e1e1e1;
}
body.dark-mode .formulario input:not([type=submit]),
body.dark-mode .formulario textarea,
body.dark-mode .formulario select {
  background-color: #e6e6e6;
  border: none;
  font-size: 1.8rem;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
body.dark-mode table.propiedades tbody tr:nth-child(odd),
body.dark-mode table.vendedores tbody tr:nth-child(odd) {
  background-color: #252525;
}
body.dark-mode table.propiedades tbody td,
body.dark-mode table.vendedores tbody td {
  color: #FFFFFF;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 85rem;
  margin: 0 auto;
  width: 95%;
}
.contenido-centrado h2 {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .contenido-centrado h2 {
    margin-bottom: 3rem;
  }
}

.contenido-agrupado {
  max-width: 105rem;
}

.mb-2 {
  margin-bottom: 2rem;
}

.w-100 {
  width: 100%;
}
.w-100 .upper-case {
  text-transform: uppercase;
}
.w-100 .f-size {
  font-size: 1.7rem;
}

.text-center {
  text-align: center;
}

.m-bottom {
  margin-bottom: 2rem;
}

.d-grid {
  display: grid;
  place-items: center;
}

.btn-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  display: inline-block;
  font-weight: 800;
  font-size: 1.7rem;
  border-radius: 0.5rem;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0.5rem 1.5rem;
  text-align: center;
  margin-top: 1.5rem;
  border: none;
  font-size: 1.8rem;
}
.btn-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  display: block;
  font-weight: 800;
  font-size: 1.7rem;
  border-radius: 0.5rem;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0.5rem 1.5rem;
  text-align: center;
  margin-top: 1.5rem;
