  background-color: #c77808;
  cursor: pointer;
}
.btn-amarillo-block.adjust-padding {
  height: 4rem;
  margin-top: 1rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
  gap: 2rem;
}

.reducir-width {
  display: flex;
  flex-direction: column;
  width: 13.5rem;
}

.btn-verde {
  background-color: #71B100;
  color: #FFFFFF;
  display: inline-block;
  font-weight: 800;
  font-size: 1.7rem;
  border-radius: 0.5rem;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0.5rem 1.5rem;
  text-align: center;
  margin-top: 1.5rem;
  border: none;
}
.btn-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.btn-yellow {
  background-color: #E08709;
  color: #FFFFFF;
  display: inline-block;
  font-weight: 800;
  font-size: 1.7rem;
  border-radius: 0.5rem;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0.5rem 1.5rem;
  text-align: center;
  margin-top: 1.5rem;
  border: none;
}
.btn-yellow:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-verde,
.btn-yellow {
  padding: 0.8rem 1.5rem;
}
@media (min-width: 1024px) {
  .btn-verde,
  .btn-yellow {
    margin-top: 3rem;
  }
}

.btn-rojo-block {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  display: block;
  font-weight: 800;
  font-size: 1.7rem;
  border-radius: 0.5rem;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0.5rem 1.5rem;
  text-align: center;
  margin-top: 1.5rem;
  border: none;
