  cursor: pointer;
}
.btn-rojo-block.adjust-padding {
  height: 4rem;
  margin-top: 2rem;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  display: block;
  font-weight: 800;
  font-size: 1.7rem;
  border-radius: 0.5rem;
  text-decoration: none;
  text-transform: uppercase;
  padding: 0.5rem 1.5rem;
