}

/* Main Layout */
.webp .inicio {
  background-image: url(../img/modern-house.webp);
}

.no-webp .inicio {
  background-image: url(../img/modern-house.jpg);
}

.header {
  background-color: #333333;
}
.header.inicio {
  height: 57rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.header.inicio .bg-color {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.65); /**Forma actual **/
}
.header.inicio .contenido-header {
  padding-bottom: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    padding-bottom: 5rem;
  }
}
.header.inicio h2 {
  color: #FFFFFF;
  font-size: 3.1rem;
  font-weight: bold;
  margin: 3rem 0 1rem;
  text-align: left;
  max-width: 50rem;
  padding-left: 1rem;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .header.inicio h2 {
    line-height: 1.7;
    font-size: 3.3rem;
  }
}
.header .logo {
  text-transform: uppercase;
  margin: 0;
  font-weight: normal;
  color: #FFFFFF;
  line-height: 1.2;
}
.header .logo .bold {
  font-weight: 900;
}

.derecha {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .derecha {
    flex-direction: row-reverse;
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  height: 3.2rem;
  filter: invert(95%);
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-bottom: 0;
    padding-left: 1.5rem;
    padding-bottom: 0.5rem;
  }
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 1.5rem 0;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    gap: 4rem;
    padding: 2rem 0;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    gap: 1.5rem;
    visibility: visible;
    opacity: 1;
    height: auto;
  }
}
.navegacion a {
  display: block;
  margin-bottom: 1rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  text-align: center;
}
.navegacion a:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-bottom: 0;
  }
}
.navegacion a:hover {
  color: #71B100;
}
.navegacion a.active {
  color: #71B100;
}

/** Menu de Hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  height: 4.5rem;
}
.mobile-menu img:hover {
  cursor: pointer;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.iconos-nosotros {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    margin-top: 3rem;
  }
}

.icono {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .icono {
    margin-bottom: 0;
  }
}
.icono:last-of-type {
  margin-bottom: 0;
}
.icono img {
  height: 7rem;
}
.icono h3 {
  text-transform: uppercase;
  margin: 1rem 0;
}
.icono p {
  font-size: 1.6rem;
  line-height: 2;
  margin: 1rem 0;
  text-align: center;
}

.ad h2 {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .ad h2 {
    margin-bottom: 3rem;
  }
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  background-color: #eeeeee;
  border: 2px solid #c8c8c8;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .anuncio {
    margin-bottom: 0;
  }
}
.anuncio:last-of-type {
  margin-bottom: 0;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
@media (min-width: 768px) {
  .anuncio .contenido-anuncio {
    padding: 1.5rem;
  }
}
.anuncio .contenido-anuncio .read-more {
  color: rgb(0, 102, 255);
  text-decoration: underline;
  padding-left: 0.3rem;
  font-weight: bold;
}
.anuncio h3 {
  line-height: 1.4;
  margin: 0 0 1rem;
}
.anuncio p {
  margin: 0;
  line-height: 1.8;
}

.precio {
  color: #71B100;
  font-weight: bold;
  font-size: 2.5rem;
}

.iconos-caracteristicas {
  list-style: none;
  padding: 0;
  display: flex;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
@media (min-width: 1024px) {
  .iconos-caracteristicas li {
    gap: 1rem;
  }
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
  font-size: 1.8rem;
}

.resumen-propiedad {
  margin-bottom: 4rem;
}
.resumen-propiedad .precio {
  margin: 1rem 0 0;
  font-size: 2.7rem;
}
.resumen-propiedad .first-text {
  margin: 0;
}
.resumen-propiedad .first-text,
.resumen-propiedad .second-text {
  line-height: 2;
}
.resumen-propiedad .iconos-caracteristicas p {
  font-size: 1.8rem;
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 5rem 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-weight: 900;
}
.imagen-contacto p {
  font-size: 1.9rem;
}
.imagen-contacto h2, .imagen-contacto p {
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}

.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
  .entrada-blog:last-of-type {
    margin-bottom: 0;
  }
}

.texto-entrada {
  margin-top: 1rem;
}
.texto-entrada a {
  color: #000000;
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.2rem;
  background-color: #71B100;
  margin-top: 1rem;
}
.texto-entrada .info-meta {
  font-weight: bold;
  font-size: 1.8rem;
}
.texto-entrada span {
  color: #E08709;
  font-weight: 900;
}
.texto-entrada p {
  font-size: 1.9rem;
  line-height: 2;
}

.resumen-blog .info-meta {
  padding-left: 2rem;
  background-color: #e6e6e6;
  border-radius: 0.5rem;
}
.resumen-blog span {
  color: #E08709;
  font-weight: 900;
}
.resumen-blog .first-text {
  margin-top: 1rem;
}
.resumen-blog .first-text,
.resumen-blog .second-text {
  line-height: 2;
  background-color: #eeeeee;
  padding: 2rem;
  border-radius: 0.5rem;
}

.testimonial {
  background-color: #71B100;
  font-size: 2rem;
  padding: 1rem;
  color: #FFFFFF;
  line-height: 2;
  border-radius: 1rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  display: block;
  position: absolute;
  width: 5rem;
  height: 5rem;
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  left: -1rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
}

.formulario {
  background-color: ghostwhite;
  padding: 2rem;
}
.formulario fieldset {
  border: 1px solid #000000;
}
.formulario fieldset:nth-child(2),
.formulario fieldset:nth-child(3) {
  margin-top: 1rem;
}
.formulario legend {
  color: #333333;
  font-size: 2rem;
}
.formulario .field {
  margin-bottom: 1.5rem;
}
.formulario label {
  display: block;
  padding-left: 0.5rem;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 0.5rem;
}
.formulario label:hover {
  cursor: pointer;
}
.formulario input:not([type=submit]),
.formulario .textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 2px solid #e1e1e1;
  border-radius: 1rem;
}
.formulario .textarea {
  height: 13rem;
}
.formulario p {
  color: #333333;
  font-size: 1.5rem;
  margin: 1.5rem 0;
}

.forma-contacto {
  max-width: 25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.forma-contacto label {
  margin-bottom: 0;
  flex: 0 0 6rem;
}

.footer {
  background-color: #333333;
  margin-top: 4rem;
  padding: 1.5rem;
}
.footer .contenido-footer {
  padding-bottom: 1.5rem;
}
@media (min-width: 768px) {
  .footer .contenido-footer {
    padding-bottom: 2rem;
  }
}
.footer .barra {
  padding: 1.5rem;
}
.footer .logo {
  text-transform: uppercase;
  margin: 0;
  font-weight: normal;
  color: #FFFFFF;
  line-height: 1.2;
}
.footer .logo .bold {
  font-weight: 900;
}
.footer .navegacion a {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: opacity 0.3s linear, height 0.4s linear;
}
@media (min-width: 768px) {
  .footer .navegacion a {
    visibility: visible;
    opacity: 1;
    height: auto;
  }
}
.footer .navegacion.mostrar a {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  margin: 0;
  text-align: center;
  color: #FFFFFF;
  font-size: 1.7rem;
  margin-top: 1rem;
}

/* Paginas Internas */
@media (min-width: 768px) {
  .nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 3rem;
  }
}

.picture img {
  height: 100%;
}

.informacion-nosotros {
  margin-top: 1rem;
}
.informacion-nosotros blockquote.expert {
  font-weight: 900;
  font-size: 1.8rem;
  color: crimson;
  margin: 0;
}
.informacion-nosotros p {
  font-size: 1.7rem;
  line-height: 2;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  margin: 1rem 0;
  border-radius: 0.5rem;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: rgb(117, 0, 128);
}
.alerta.updated {
  background-color: #f0b607;
}
.alerta.deleted {
  background-color: rgb(238, 72, 12);
}

table.propiedades,
table.vendedores {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead,
table.vendedores thead {
  background-color: rgb(71, 129, 236);
}
table.propiedades thead th,
table.vendedores thead th {
  color: #FFFFFF;
  padding: 1rem;
  font-size: 1.8rem;
  text-transform: uppercase;
}
table.propiedades tbody td,
table.vendedores tbody td {
  font-size: 1.6rem;
  font-weight: bold;
  text-transform: uppercase;
}
table.propiedades tbody .size,
table.vendedores tbody .size {
  font-size: 1.8rem;
}
table.propiedades .imagen-tabla,
table.vendedores .imagen-tabla {
  width: 13rem;
  display: block;
  margin: 0 auto;
}

.picture-small {
  width: 30rem;
  margin-bottom: 1.5rem;
}